@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&family=VT323&display=swap);
* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-image: url(/battleship/static/media/sea.e691ca44.jpg);
  background-size: cover;
  background-repeat: no-repeat;

  color: rgb(29, 29, 29);
}

html,
body,
button,
input,
h3 {
  font-family: 'VT323', monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
    outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
    -webkit-appearance: none;
    -moz-appearance: none;
}

input[type=search] {
    -webkit-appearance: none;
    -moz-appearance: none;
    box-sizing: content-box;
}

textarea {
    overflow: auto;
    vertical-align: top;
    resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */

audio,
canvas,
video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
    max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */

audio:not([controls]) {
    display: none;
    height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */

[hidden] {
    display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */

html {
    font-size: 100%; /* 1 */
    -webkit-text-size-adjust: 100%; /* 2 */
    -ms-text-size-adjust: 100%; /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */

a:focus {
    outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */

a:active,
a:hover {
    outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */

img {
    border: 0; /* 1 */
    -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */

figure {
    margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */

form {
    margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */

fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */

legend {
    border: 0; /* 1 */
    padding: 0;
    white-space: normal; /* 2 */
    *margin-left: -7px; /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */

button,
input,
select,
textarea {
    font-size: 100%; /* 1 */
    margin: 0; /* 2 */
    vertical-align: baseline; /* 3 */
    *vertical-align: middle; /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */

button,
input {
    line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */

button,
select {
    text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */

button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button; /* 2 */
    cursor: pointer; /* 3 */
    *overflow: visible;  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
    cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */

input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box; /* 1 */
    padding: 0; /* 2 */
    *height: 13px; /* 3 */
    *width: 13px; /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */

input[type="search"] {
    -webkit-appearance: textfield; /* 1 */ /* 2 */
    box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */

button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */

textarea {
    overflow: auto; /* 1 */
    vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */

table {
    border-collapse: collapse;
    border-spacing: 0;
}

html,
button,
input,
select,
textarea {
    color: #222;
}

::selection {
    background: #b3d4fc;
    text-shadow: none;
}

img {
    vertical-align: middle;
}

fieldset {
    border: 0;
    margin: 0;
    padding: 0;
}

textarea {
    resize: vertical;
}

.chromeframe {
    margin: 0.2em 0;
    background: #ccc;
    color: #000;
    padding: 0.2em 0;
}


.wrap {
  width: 95%;
  max-width: 1350px;
  margin: 0 auto;
}

h1, h2, h4, h5, h6 {
  font-family: 'Black Ops One', serif;
}

h1 {
  font-size: 5.0rem;
}

h2 {
  font-size: 3.0rem;
}

p,
button,
input {
  font-size: 2.0rem;
}

p.smaller-p {
  font-size: 1.6rem;
}

input,
button {
  padding: 7px 20px;
  margin: 0 10px;
  border-radius: 3px;
  border: 2px solid #000;
}

input {
  background-color: #fff;
}

button,
input[type="submit"] {
  background-color: rgb(0, 21, 65);
  color: #fff;
  cursor: pointer;
  transition: 0.2s;
}

  button:enabled:hover,
  input[type="submit"]:hover {
    background-color: rgb(24, 50, 104);
  }

  button:disabled {
    background-color: rgb(77, 84, 102);
    border: 2px solid rgb(66, 66, 66);
    cursor: not-allowed;
  }

.App {
  text-align: center;
}

.App h1,
.App h2 {
  text-transform: uppercase;
  text-shadow: 0 0 15px #5d6aa299;
  color: rgb(0, 21, 65);
  margin: 60px 0 80px 0;
}

.App h2 {
  margin-bottom: 30px;
}

.App p {
  margin-bottom: 20px;
}

.fade-out {
  transition: 1s;
  opacity: 0;
}

div.forty {
  width: 40%;
  padding: 0 10px;
  display: inline-block;
}

div.twenty {
  width: 20%;
  padding: 0 10px;
  display: inline-block;
  vertical-align: top;
  font-size: 1.2rem;
}

.white {
  color: #fff;
}
.gameboard {
  width: 400px;
  height: 400px;
  background: rgb(66, 81, 124);
  margin: 0 auto 20px auto;
  display: grid;
  grid-template-columns: repeat(10, 10%);
  opacity: 0.95;
  position: relative;
  background-image: linear-gradient(to top, rgba(66, 81, 124, .9), rgba(66, 81, 124, .9)),
                    url(/battleship/static/media/sea-board.c6408315.jpg);
  background-size: cover;
  box-shadow: 0px 0px 20px -1px #00000063;
}

.gameboard::before {
  content: '';
  width: 100%;
  height: 100%;
  border: 2px solid #000;
  position: absolute;
  top: -2px;
  left: -2px;
  z-index: -1;
}

.gameboard-tile,
.gameboard-tile-occupied {
  position: relative;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.gameboard-tile {
  background-color: transparent;
  border: 2px solid #000;
}

.gameboard-tile-occupied {
  background-color: rgb(56, 56, 56);
  border: 2px solid #000;
}

.gameboard-tile-hover-valid {
  border: 2px solid rgb(25, 136, 44);
}

.gameboard-tile-hover-invalid {
  border: 2px solid rgb(136, 25, 31);
}

.gameboard-tile-hover-attack-valid {
  border: 2px solid rgb(228, 155, 19);
  animation: attackBorder 0.3s;
  animation-iteration-count: infinite;
  animation-direction: alternate-reverse;
}

@keyframes attackBorder {
  0% {
    border: 2px solid rgb(228, 155, 19);
  }

  100% {
    border: 2px solid rgb(228, 68, 19);
  }
}

.gameboard-tile-hover-attack-invalid {
  border: 2px solid rgb(136, 25, 31);
}

.gameboard-placing-controls {
  position: absolute;
  top: 0;
  left: 110%;
  width: 100%;
}

.gameboard-placing-controls p,
.gameboard-placing-controls button {
  margin-bottom: 20px;
}

.hit,
.miss {
  width: 80%;
  height: 80%;
  position: absolute;
  top: 10%;
  left: 10%;
  opacity: 0.7;
}

.hit {
  background-color: rgba(255, 0, 0, 0.1);
}

.miss {
  background-color: transparent;
  opacity: 0.65;
  width: 70%;
  height: 70%;
  top: 15%;
  left: 15%;
}

.game-log {
  height: 400px;
  overflow-y: hidden;
  position: relative;
}

.game-log p {
  font-size: 1.4rem;
}

.game-log p:nth-of-type(2) {
  opacity: 1;
}

.game-log p:nth-of-type(3) {
  opacity: 0.6;
}

.game-log p:nth-of-type(4) {
  opacity: 0.4;
}

.game-log p:nth-of-type(5) {
  opacity: 0.3;
}

.game-log p:nth-of-type(6) {
  opacity: 0.2;
}

.game-log p.highlight {
  color: rgb(0, 252, 217);
}

.game-log p.warning {
  color: rgb(150, 0, 0);
}

.game-log h3.game-log-win-header {
  color: rgb(19, 253, 117);
  text-decoration: none;
  animation-name: gameWin;
  animation-duration: 2s;
  animation-delay: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

@keyframes gameWin {
  0%, 75% {
    color: rgb(19, 253, 117);
  }

  75%, 80% {
    color: rgb(40, 236, 122);
  }

  81%, 85% {
    color: rgb(19, 253, 117);
  }

  86%, 90% {
    color: rgb(40, 236, 122);
  }

  91%, 95% {
    color: rgb(19, 253, 117);
  }

  96%, 100% {
    color: rgb(40, 236, 122);
  }
}

.game-log h3.game-log-loss-header {
  color: rgb(194, 0, 0);
  text-decoration: none;
  animation-name: gameLoss;
  animation-duration: 2s;
  animation-delay: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

@keyframes gameLoss {
  0%, 75% {
    color: rgb(194, 0, 0);
  }

  75%, 80% {
    color: rgb(175, 2, 2);
  }

  81%, 85% {
    color: rgb(194, 0, 0);
  }

  86%, 90% {
    color: rgb(175, 2, 2);
  }

  91%, 95% {
    color: rgb(194, 0, 0);
  }

  96%, 100% {
    color: rgb(175, 2, 2);
  }
}

.game-log h3.game-log-win-header,
.game-log h3.game-log-loss-header {
  margin-top: 30px;
  margin-bottom: 15px;
  font-size: 1.8rem;
  text-shadow: 1px 1px 5px #4a4d49;
}

.game-log p.game-log-win {
  color: rgb(241, 255, 243);
  margin-bottom: 400px;
  text-shadow: 1px 1px 5px #4a4d49;
}

.game-log p.game-log-loss {
  color: rgb(255, 241, 241);
  margin-bottom: 400px;
  text-shadow: 1px 1px 5px #4a4d49;
}

.game-log h3:first-of-type {
  margin-bottom: 20px;
  font-size: 2.0rem;
  text-decoration: underline;
  text-transform: uppercase;
}

.game-log-win-header,
.game-log-loss-header {
  font-family: 'Black Ops One', serif;
}

.passing-device h3 {
  font-size: 2.0rem;
  color: rgb(0, 0, 0);
  margin-bottom: 20px;
  animation: 2s ease-in-out 0s infinite alternate-reverse forwards flashSlowly;
}

button.pass-device {
  margin: 15px 0 0 0;
  animation-name: flashBorder;
  animation-duration: .5s;
  animation-iteration-count: infinite;
  animation-direction: alternate-reverse;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

@keyframes flashSlowly {
  0% {
    color: rgb(66, 74, 110);
  }

  100% {
    color: rgb(0, 0, 0);
  }
}

button.play-again {
  opacity: 0;
  width: 100%;
  margin: 15px 0 0 0;
  animation-name: flashBorder, fadeIn;
  animation-delay: 5s, 1s;
  animation-duration: .5s, .5s;
  animation-iteration-count: infinite, 1;
  animation-direction: alternate-reverse, normal;
  animation-fill-mode: forwards, forwards;
  animation-timing-function: ease-in-out, ease-in-out;
}

@keyframes flashBorder {
  0% {
    border: 2px solid rgb(46, 47, 48);
    transform: scale(0.98);
  }

  100% {
    border: 2px solid #000;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 100;
  }
}


@media only screen and (max-height: 650px) {
  .gameboard {
    width: 300px;
    height: 300px;
  }

  .game-log {
    height: 300px;
  }

  .game-log h3:first-of-type,
  .game-log h3.game-log-win-header,
  .game-log h3.game-log-loss-header {
    font-size: 1.6rem;
  }
}
